.SmallTodo {
    text-align: center;
}

.SmallTodo .item-todo {
    padding: 14px;
    width: 250px;
    margin: 0 auto;
    text-align: left;
    margin-top: 15px;
    background: linear-gradient(
            90deg,
            rgb(93, 12, 255) 0%,
            rgba(155, 0, 250, 1) 100%
    );
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.SmallTodo .item-todo .item-text {
    display: inline-block;
    cursor: pointer;
}

.SmallTodo input {
    padding: 14px 32px 14px 16px;
    border-radius: 4px 0 0 4px;
    border: 2px solid #5d0cff;
    outline: none;
    background: transparent;
}

.SmallTodo button {
    padding: 16px 7px;
    border: none;
    border-radius: 0 4px 4px 0;
    cursor: pointer;
    outline: none;
    background: linear-gradient(
            90deg,
            rgba(93, 12, 255, 1) 0%,
            rgba(155, 0, 250, 1) 100%
    );
    color: #fff;
    text-transform: capitalize;
}

.SmallTodo .item-todo .item-delete {
    display: inline-block;
    cursor: pointer;
    float: right;
    font-size: 22px;
    position: relative;
    left: -4px;
    bottom: 5px;
}

.strike {
    text-decoration: line-through;
}
.width-100percent {
    width: 100%;
}
.max-width-50percent {
    max-width: 50%;
}

.app-wrapper {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 10px;
}

.listgroup {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 50%;
}

.forms {
    margin: 6px;
    width: 90%;
}

.userimg {
    width: 100px;
    height: 100px;
}

.btn-add {
    width: 100%;
}

.accordion-toggle {
    margin: 4px;
}
