@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@700&display=swap');

*{
  margin: 0;
  padding: 0;
  list-style: none;
  outline: none;
  font-family: Roboto, -apple-system, sistem-ui sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
html, body {
  height: 100%;
}

#cube-loader {
  display: flex;
  height: 100%;
  width: 100%;
  position: inherit;
  & .caption {
    margin: 0 auto;
  }
  .cube-loader {
    width: 73px;
    height: 73px;
    margin: 49px auto;
    position: relative;
    transform: rotateZ(45deg);

    & .cube {
      position: relative;
      transform: rotateZ(45deg);
      width: 50%;
      height: 50%;
      float: left;
      transform: scale(1.1);
    }
    & .cube:before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(52, 73, 94, 1.0);
      animation: cube-loader 2.76s infinite linear both;
      transform-origin: 100% 100%;
    }
    & .loader-2 {
      transform: scale(1.1) rotateZ(90deg);
    }
    & .loader-3 {
      transform: scale(1.1) rotateZ(180deg);
    }
    & .loader-4 {
      transform: scale(1.1) rotateZ(270deg);
    }
    & .loader-2:before {
      animation-delay: 0.35s;
    }
    & .loader-3:before {
      animation-delay: 0.69s;
    }
    & .loader-4:before {
      animation-delay: 1.04s;
    }
  }
}

@keyframes cube-loader {
  0%, 10% {
    transform: perspective(136px) rotateX(-180deg);
    opacity: 0;
  }
  25%, 75% {
    transform: perspective(136px) rotateX(0deg);
    opacity: 1;
  }
  90%, 100% {
    transform: perspective(136px) rotateY(180deg);
    opacity: 0;
  }
}

.todo {
  display: flex;
  position: absolute;
  top: 10%;
  width: 750px;
  min-height: calc(100vh - 200px);
  box-shadow: 1px 2px 20px #dddddd;
  border-radius: 10px;
  border: 1px solid #f4f6f8;
  font-size: 14px;

  &__sidebar{
    background-color: #efefef;
    width: 230px;
    min-height: 100%;
    border-radius: 10px 0 0 10px;
    border-right: 1px solid #f1f1f1;
    padding: 60px 20px;
  }
  &__tasks{
    flex:1;
    padding: 60px 40px 60px 40px;
    overflow: auto;
    max-height: 760px;
  }
}

.field {
  background: #fff;
  border: 1px solid #dedede;
  box-sizing: border-box;
  border-radius: 4px;
  padding: 8px 12px;
  width: 100%;
  font-size: 14px;
  &:focus {
    border-color: #cfcfcf;
  }
}

.button{
  background: #4DD599;
  border-radius: 4px;
  color: #fff;
  border: 0;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 14px;
  &:hover{
    background: darken(#4DD599, 7%);
  }
  &:active{
    background: darken(#4DD599, 12%);
  }
  &:disabled{
    background: #d8d8d8;
  }
  &__grey{
    background: #e0e0e0;
    color: #444;
    &:hover{
      background: darken(#e0e0e0, 3%);
    }
    &:active{
      background: darken(#e0e0e0, 6%);
    }
  }
}